<template>
    <div class="container" @wheel.prevent="wheelHandler">
        <div class="relax" v-rellax="{speed: 2, vertical: false, horizontal: true}">
            <scale :startDate="this.sc.startDate" :endDate="this.sc.endDate" :step="this.sc.step" :ratio="this.sc.ratio"/>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import Scale from '@/components/Scale'

    export default {
        name: "Home",
        props: {
            sc: Scale
        },
        components: {
            Scale
        },

        data() {
            return {
                scale: {}
            }
        },

        created() {
            this.getData();
        },

        methods: {
            wheelHandler(event) {
                event.view.scrollBy({left: event.deltaY, top: 0, behavior: 'smooth'})
            },

            async getData() {
                try {
                    const result = (await axios.get('/getScale')).data;
                    if (result) {
                        this.sc = result
                    }
                }
                catch (e) {
                    console.error(e)
                }
            }
        }
    }

</script>

